import React, { useState } from "react";
import { motion } from "framer-motion";
import GoldenSectionOptimizer from "./GoldenSection";
import FibonacciOptimizer from "./Fibonacci";
import SequentialOptimizer from "./SequentialSearch";
import DichotomousOptimizer from "./DichotomousSearch";
import BisectionOptimizer from "./Bisection";
import NewtonOptimizer from "./Newton";

const OPTIMIZATION_METHODS = {
  "Seccin Aurea": GoldenSectionOptimizer,
  "Fibonacci": FibonacciOptimizer,
  "Bsqueda secuencial": SequentialOptimizer,
  "Bsqueda dicotmica": DichotomousOptimizer,
  "Biseccin": BisectionOptimizer,
  "Newton unidimensional": NewtonOptimizer,
};

export default function OptimizationMethodSelector() {
  const [selectedMethod, setSelectedMethod] = useState("Seccin Aurea");

  const SelectedComponent = OPTIMIZATION_METHODS[selectedMethod];

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header with method selector */}
      <div className="bg-white shadow-sm border-b">
        <div className="max-w-5xl mx-auto px-6 py-4">
          <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
            <div>
              <h1 className="text-2xl font-bold text-gray-900">Optimizador Unidimensional</h1>
              <p className="text-sm text-gray-600 mt-1">Selecciona un mtodo de optimizacin para minimizar funciones</p>
            </div>
            
            <div className="flex items-center gap-4">
              <label className="text-sm font-medium text-gray-700">Mtodo:</label>
              <select 
                value={selectedMethod} 
                onChange={(e) => setSelectedMethod(e.target.value)}
                className="px-4 py-2 border border-gray-300 rounded-lg bg-white text-sm focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500"
              >
                {Object.keys(OPTIMIZATION_METHODS).map((method) => (
                  <option key={method} value={method}>
                    {method}
                  </option>
                ))}
              </select>
            </div>
          </div>
        </div>
      </div>

      {/* Method description */}
      <div className="bg-indigo-50 border-b">
        <div className="max-w-5xl mx-auto px-6 py-3">
          <div className="text-sm text-indigo-800">
            <strong>Mtodo seleccionado:</strong> {selectedMethod} - 
            {selectedMethod === "Seccin Aurea" && " Utiliza la proporcin urea para dividir el intervalo de bsqueda de manera ptima."}
            {selectedMethod === "Fibonacci" && " Emplea la secuencia de Fibonacci para determinar los puntos de evaluacin de manera eficiente."}
            {selectedMethod === "Bsqueda secuencial" && " Evala la funcin en puntos equidistantes para encontrar el mnimo mediante bsqueda exhaustiva."}
            {selectedMethod === "Bsqueda dicotmica" && " Divide el intervalo en dos partes iguales y evala puntos cercanos al centro."}
            {selectedMethod === "Biseccin" && " Encuentra la raz de la derivada (punto crtico) dividiendo el intervalo por la mitad."}
            {selectedMethod === "Newton unidimensional" && " Utiliza la primera y segunda derivada para encontrar el mnimo con convergencia cuadrtica."}
          </div>
          
          {/* Legend */}
          <div className="mt-3 flex flex-wrap gap-4 text-xs">
            <div className="flex items-center gap-2">
              <div className="w-3 h-3 bg-red-500 rounded-full border-2 border-white"></div>
              <span className="text-indigo-700"><strong>Punto mnimo encontrado</strong></span>
            </div>
            <div className="flex items-center gap-2">
              <div className="w-3 h-3 bg-orange-500 rounded-full"></div>
              <span className="text-indigo-700">Puntos de evaluacin</span>
            </div>
            <div className="flex items-center gap-2">
              <div className="w-3 h-3 bg-blue-500 rounded-full"></div>
              <span className="text-indigo-700">Puntos auxiliares</span>
            </div>
          </div>
        </div>
      </div>

      {/* Selected optimization component */}
      <motion.div
        key={selectedMethod}
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.3 }}
      >
        <SelectedComponent />
      </motion.div>
    </div>
  );
}
